/* ======== OWL animation ======== */

.animated {
  -webkit-animation-duration: 1s !important;
          animation-duration: 1s !important;
  -webkit-animation-timing-function: cubic-bezier(0.33, 1.33, 1, 1);
          animation-timing-function: cubic-bezier(0.33, 1.33, 1, 1); }

.owl-animated-out {
  z-index: 1; }

.owl-animated-in {
  z-index: 0; }

.fadeOut {
  -webkit-animation-name: fadeOut;
          animation-name: fadeOut; }

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    opacity: 0;
    -webkit-transform: translateX(100px);
            transform: translateX(100px); }
  100% {
    opacity: 0;
    -webkit-transform: translateX(100px);
            transform: translateX(100px); } }

@keyframes fadeOut {
  0% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  50% {
    opacity: 0;
    -webkit-transform: translateX(100px);
            transform: translateX(100px); }
  100% {
    opacity: 0;
    -webkit-transform: translateX(100px);
            transform: translateX(100px); } }

.flipInX {
  -webkit-animation-name: flipInX;
          animation-name: flipInX; }

@-webkit-keyframes flipInX {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px); }
  40% {
    opacity: 0;
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px); }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

@keyframes flipInX {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px); }
  40% {
    opacity: 0;
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px); }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0); } }

.owl-nav {
  position: absolute;
  width: 100%;
  top: 50%;
  right: 0;
  left: 0;
  height: 33px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }

.owl-nav .owl-prev {
    position: absolute;
    display: inline-block;
    left: -18px;
    opacity: 0.5;
    -webkit-transition: opacity .15s ease-in-out;
    transition: opacity .15s ease-in-out; }

.owl-nav .owl-prev:hover {
      opacity: 1; }

.owl-nav .owl-prev:active {
      opacity: 0.5; }

.owl-nav .owl-next {
    position: absolute;
    display: inline-block;
    right: -18px;
    opacity: 0.5;
    -webkit-transition: opacity .15s ease-in-out;
    transition: opacity .15s ease-in-out; }

